// settings.ts
import { dataViewObjectsParser } from "powerbi-visuals-utils-dataviewutils";
import DataViewObjectsParser = dataViewObjectsParser.DataViewObjectsParser;

export class VisualSettings extends DataViewObjectsParser {
    public visualSettings = new VisualSettingsOptions();
}

export class VisualSettingsOptions {
    // Task appearance settings
    public taskColor: string = "#0078D4";     // Default blue color for regular tasks
    public criticalPathColor: string = "#E81123"; // Default red for critical path
    public milestoneColor: string = "#555555"; // Default dark gray for milestones
    public taskOpacity: number = 0.8;         // Task opacity (0-1)
    
    // Text settings
    public fontSize: number = 11;             // Base font size in pixels
    public labelColor: string = "#252525";    // Label text color
    public maxLabelLength: number = 40;       // Maximum characters for labels before truncating
    
    // Visual controls
    public maxTasksToShow: number = 40;       // Maximum number of tasks to display
    public leftMargin: number = 280;          // Left margin size to accommodate labels
    public showDuration: boolean = true;      // Show duration text on tasks
    public showTooltips: boolean = true;      // Show tooltips on hover
    
    // Methods for settings access with validation
    public fontSizeValue(): number {
        return this.validateNumberSetting(this.fontSize, 9, 16, 11);
    }
    
    public maxTasksToShowValue(): number {
        return this.validateNumberSetting(this.maxTasksToShow, 10, 100, 40);
    }
    
    public leftMarginValue(): number {
        return this.validateNumberSetting(this.leftMargin, 150, 500, 280);
    }
    
    public maxLabelLengthValue(): number {
        return this.validateNumberSetting(this.maxLabelLength, 10, 100, 40);
    }
    
    public taskColorValue(): string {
        return this.validateColorSetting(this.taskColor, "#0078D4");
    }
    
    public criticalPathColorValue(): string {
        return this.validateColorSetting(this.criticalPathColor, "#E81123");
    }
    
    public milestoneColorValue(): string {
        return this.validateColorSetting(this.milestoneColor, "#555555");
    }
    
    public labelColorValue(): string {
        return this.validateColorSetting(this.labelColor, "#252525");
    }
    
    public showDurationValue(): boolean {
        return this.showDuration !== false; // Default to true if undefined
    }
    
    public showTooltipsValue(): boolean {
        return this.showTooltips !== false; // Default to true if undefined
    }
    
    // Validation helpers
    private validateNumberSetting(value: any, min: number, max: number, defaultValue: number): number {
        const num = +value; // Convert to number
        if (isNaN(num) || num < min || num > max) {
            return defaultValue;
        }
        return num;
    }
    
    private validateColorSetting(value: any, defaultValue: string): string {
        if (!value || typeof value !== 'string' || !value.match(/^#[0-9A-Fa-f]{6}$/)) {
            return defaultValue;
        }
        return value;
    }
}