// Import necessary modules from Power BI visuals API and D3.js
import * as d3 from "d3";
import powerbi from "powerbi-visuals-api";
import IVisualHost = powerbi.extensibility.visual.IVisualHost;
import VisualUpdateOptions = powerbi.extensibility.visual.VisualUpdateOptions;
import VisualConstructorOptions = powerbi.extensibility.visual.VisualConstructorOptions;
import DataView = powerbi.DataView;
import IViewport = powerbi.IViewport;
import Fill = powerbi.Fill;
import IVisual = powerbi.extensibility.visual.IVisual;
import VisualObjectInstance = powerbi.VisualObjectInstance;
import VisualObjectInstanceEnumerationObject = powerbi.VisualObjectInstanceEnumerationObject;
import VisualObjectInstanceEnumeration = powerbi.VisualObjectInstanceEnumeration;
import EnumerateVisualObjectInstancesOptions = powerbi.EnumerateVisualObjectInstancesOptions;
import PrimitiveValue = powerbi.PrimitiveValue;

// Import the visual settings class
import { VisualSettings } from "./settings";

// Define interfaces for our task data structure
interface Task {
    id: string | number;
    internalId: string; // Use string for map keys and comparisons
    name: string;
    type: string; // e.g., 'TT_Task', 'TT_Mile'
    duration: number;
    finishDateStr: string; // Original finish date string
    predecessorIds: string[]; // IDs of direct predecessors (use string)
    relationshipTypes: { [predId: string]: string }; // Type of relationship from predecessor (use string key)
    relationshipFreeFloats: { [predId: string]: number | null }; // Free float from predecessor (use string key)
    successors: Task[]; // Populated later
    predecessors: Task[]; // Populated later
    earlyStart: number;
    earlyFinish: number;
    lateStart: number;
    lateFinish: number;
    totalFloat: number;
    isCritical: boolean; // Final criticality status
    isCriticalByFloat?: boolean; // Helper flag: Critical based on Total Float (LS-ES)
    isCriticalByRel?: boolean; // Helper flag: Critical based on being part of a critical relationship
    yOrder?: number; // Used for plotting order
}

interface Relationship {
    predecessorId: string; // Use string ID
    successorId: string; // Use string ID
    type: string;
    freeFloat: number | null;
    isCritical: boolean; // Is the relationship itself critical?
}

// Main Visual Class
export class Visual implements IVisual {
    private target: HTMLElement; // The HTML element the visual is rendered in
    private host: IVisualHost; // Provides services from Power BI host (tooltips, selections, color palette)
    private svg: d3.Selection<SVGSVGElement, unknown, null, undefined>; // Main SVG container
    private mainGroup: d3.Selection<SVGGElement, unknown, null, undefined>; // Group for chart elements (for margins)
    private settings: VisualSettings; // Holds formatting settings
    private data: Task[] = []; // Processed task data
    private relationships: Relationship[] = []; // Processed relationships
    private taskIdToTask: Map<string, Task> = new Map(); // For quick task lookups by ID

    // Default layout constants (can be overridden by settings)
    private margin = { top: 30, right: 180, bottom: 70, left: 280 };
    private taskHeight = 22;
    private taskPadding = 12;
    private milestoneSize = 12;
    private arrowHeadSize = 6;
    private labelOffset = 15;
    private dateLabelOffset = 8;
    private durationTextPadding = 5;
    private arrowStrokeWidth = 1.5;
    private criticalArrowStrokeWidth = 2.5;
    private arrowElbowOffset = 15;
    private floatTolerance = 0.001;
    private defaultMaxTasks = 40;

    // Constructor
    constructor(options: VisualConstructorOptions) {
        console.log("--- Initializing Critical Path Visual ---");
        this.target = options.element;
        this.host = options.host;
        this.svg = d3.select(this.target).append("svg").classed("criticalPathVisual", true);
        this.mainGroup = this.svg.append("g").classed("main-group", true);
        this.createArrowheadMarkers();
        
        // Create tooltip div if not exists
        if (!d3.select("body").select(".tooltip").size()) {
            d3.select("body").append("div")
                .attr("class", "tooltip")
                .style("position", "absolute")
                .style("visibility", "hidden")
                .style("background-color", "white")
                .style("border", "1px solid #ddd")
                .style("border-radius", "3px")
                .style("padding", "10px")
                .style("box-shadow", "0 0 5px rgba(0,0,0,0.2)")
                .style("pointer-events", "none")
                .style("z-index", "1000");
        }
    }

    // Create arrowhead markers
    private createArrowheadMarkers(): void {
        const defs = this.svg.append("defs");
        
        // Critical Arrowhead
        defs.append("marker")
            .attr("id", "arrowhead-critical")
            .attr("viewBox", "0 -5 10 10")
            .attr("refX", 5)
            .attr("refY", 0)
            .attr("markerWidth", this.arrowHeadSize)
            .attr("markerHeight", this.arrowHeadSize)
            .attr("orient", "auto")
            .append("path")
            .attr("d", "M0,-5L10,0L0,5")
            .attr("class", "arrowhead arrowhead-critical");
        
        // Non-critical Arrowhead
        defs.append("marker")
            .attr("id", "arrowhead")
            .attr("viewBox", "0 -5 10 10")
            .attr("refX", 5)
            .attr("refY", 0)
            .attr("markerWidth", this.arrowHeadSize)
            .attr("markerHeight", this.arrowHeadSize)
            .attr("orient", "auto")
            .append("path")
            .attr("d", "M0,-5L10,0L0,5")
            .attr("class", "arrowhead arrowhead-non-critical");
    }

    // Update method
    public update(options: VisualUpdateOptions) {
        try {
            console.log("--- Script Start ---");
            this.settings = VisualSettings.parse(options.dataViews[0]) as VisualSettings;
            const dataView = options.dataViews[0];
            const viewport = options.viewport;
            
            // Apply settings to margins
            this.margin.left = this.settings.visualSettings.leftMarginValue();
            
            this.clearVisual();

            if (!this.validateDataView(dataView)) {
                this.displayMessage("Missing required data fields (Task ID, Duration).");
                return;
            }

            console.log("--- Debug: Starting data transformation ---");
            this.transformData(dataView);

            if (this.data.length === 0) {
                this.displayMessage("No task data to display after processing.");
                return;
            }

            console.log(`--- Debug: Found ${this.data.length} tasks after data transformation ---`);
            
            console.log("--- Debug: Starting CPM calculations ---");
            this.calculateCPM();

            const criticalPathTasks = this.data.filter(task => task.isCritical)
                .sort((a, b) => (a.earlyStart || 0) - (b.earlyStart || 0));
            
            console.log(`--- Debug: Found ${criticalPathTasks.length} critical tasks ---`);
            
            const maxTasksToShow = this.settings.visualSettings.maxTasksToShowValue();
            const tasksToSort = this.data.sort((a, b) => (a.earlyStart || 0) - (b.earlyStart || 0));
            
            // Determine which tasks to show (prioritize critical path)
            const tasksToShow = (criticalPathTasks.length > 0)
                ? this.limitTasks(criticalPathTasks, maxTasksToShow)
                : this.limitTasks(tasksToSort, maxTasksToShow);

            if (tasksToShow.length === 0) {
                this.displayMessage("No tasks to display after filtering/limiting.");
                return;
            }

            console.log(`--- Debug: Showing ${tasksToShow.length} tasks after limiting ---`);
            
            // Assign vertical positions for visualization
            tasksToShow.forEach((task, index) => {
                task.yOrder = index;
            });

            console.log("--- Debug: Starting visualization ---");
            this.setupSVGAndScales(viewport, tasksToShow);
            this.drawVisualElements(tasksToShow);
            
            console.log("--- Script End ---");
        } catch (error) {
            console.error("ERROR during update:", error);
            this.displayMessage(`Error: ${error instanceof Error ? error.message : String(error)}`);
        }
    }

    // Clear previous visualization
    private clearVisual(): void {
        this.mainGroup.selectAll("*").remove();
        this.svg.selectAll(".message-text, .project-duration-text, .project-end-line, .project-end-text").remove();
        this.taskIdToTask.clear();
    }

    // Validate the data view has required fields
    private validateDataView(dataView: DataView): boolean {
        if (!dataView || !dataView.table || !dataView.table.rows || !dataView.table.columns) {
            console.log("--- ERROR: Invalid data view structure ---");
            return false;
        }
        
        const hasTaskId = this.hasDataRole(dataView, 'taskId');
        const hasDuration = this.hasDataRole(dataView, 'duration');
        
        console.log(`--- Debug: Data validation - Has TaskId: ${hasTaskId}, Has Duration: ${hasDuration} ---`);
        
        return hasTaskId && hasDuration;
    }

    // Transform data from Power BI format to our internal model
    private transformData(dataView: DataView): void {
        console.log("--- Debug: Starting data transformation ---");
        this.data = [];
        this.relationships = [];
        this.taskIdToTask.clear();
        
        if (!dataView.table || !dataView.table.rows) {
            console.log("--- ERROR: No data table found ---");
            return;
        }

        const table = dataView.table;
        const rows = table.rows;
        console.log(`--- Debug: Processing ${rows.length} rows ---`);

        // Get column indices for all needed fields
        const idIdx = this.getColumnIndex(dataView, 'taskId');
        const nameIdx = this.getColumnIndex(dataView, 'taskName');
        const typeIdx = this.getColumnIndex(dataView, 'taskType');
        const durationIdx = this.getColumnIndex(dataView, 'duration');
        const finishDateIdx = this.getColumnIndex(dataView, 'finishDate');
        const predIdIdx = this.getColumnIndex(dataView, 'predecessorId');
        const relTypeIdx = this.getColumnIndex(dataView, 'relationshipType');
        const relFloatIdx = this.getColumnIndex(dataView, 'relationshipFreeFloat');

        console.log(`--- Debug: Column indices - ID: ${idIdx}, Name: ${nameIdx}, Type: ${typeIdx}, Duration: ${durationIdx} ---`);
        
        // --- First pass: Create Task objects ---
        console.log("--- Debug: Starting first processing loop (Task Details) ---");
        let processedCount1 = 0;
        
        rows.forEach((row, rowIndex) => {
            // Skip rows with missing task IDs
            if (idIdx === -1 || row[idIdx] === null || row[idIdx] === undefined) {
                console.log(`Row ${rowIndex}: Skipping due to missing task ID`);
                return;
            }

            const rawTaskId = row[idIdx];
            
            // Handle type conversion for task ID - ensure it's string or number
            let taskId: string | number;
            if (typeof rawTaskId === 'string' || typeof rawTaskId === 'number') {
                taskId = rawTaskId; // Direct assignment if already string or number
            } else {
                // Convert other types to string
                taskId = String(rawTaskId);
            }
            
            const taskIdStr = String(taskId); // Convert to string for consistent lookups
            
            // Skip if we've already processed this task
            if (this.taskIdToTask.has(taskIdStr)) {
                return;
            }

            // Extract task properties with proper fallbacks
            const taskName = (nameIdx !== -1 && row[nameIdx] != null) 
                ? String(row[nameIdx]) 
                : taskIdStr;
                
            const taskType = (typeIdx !== -1 && row[typeIdx] != null) 
                ? String(row[typeIdx]) 
                : 'TT_Task';
                
            // Parse duration with proper handling for milestones and invalid values
            let duration = 0;
            if (durationIdx !== -1 && row[durationIdx] != null) {
                const parsedDuration = Number(row[durationIdx]);
                if (!isNaN(parsedDuration)) {
                    duration = parsedDuration;
                }
            }
            
            // Milestones always have 0 duration
            if (taskType === 'TT_Mile' || taskType === 'TT_FinMile') {
                duration = 0;
            }
            
            // Ensure duration is non-negative
            duration = Math.max(0, duration);

            // Format finish date string
            const finishDateStr = (finishDateIdx !== -1 && row[finishDateIdx] != null) 
                ? this.cleanDate(String(row[finishDateIdx])) 
                : "";

            // Create task object
            const task: Task = {
                id: taskId,
                internalId: taskIdStr,
                name: taskName,
                type: taskType,
                duration: duration,
                finishDateStr: finishDateStr,
                predecessorIds: [],
                predecessors: [],
                successors: [],
                relationshipTypes: {},
                relationshipFreeFloats: {},
                earlyStart: 0,
                earlyFinish: duration,
                lateStart: Infinity,
                lateFinish: Infinity,
                totalFloat: Infinity,
                isCritical: false
            };

            // Store the task
            this.data.push(task);
            this.taskIdToTask.set(taskIdStr, task);
            processedCount1++;
        });

        console.log(`--- Debug: First pass completed. Created ${processedCount1} tasks ---`);

        // --- Second pass: Build relationship network ---
        console.log("--- Debug: Starting second processing loop (Relationships) ---");
        let processedRelCount = 0;
        let skippedRelCount = 0;
        
        rows.forEach((row, rowIndex) => {
            // Skip if no predecessor ID column or missing value
            if (predIdIdx === -1 || row[predIdIdx] == null || String(row[predIdIdx]).trim() === '') {
                return;
            }

            const rawTaskId = row[idIdx];
            const rawPredId = row[predIdIdx];
            
            if (rawTaskId == null) {
                console.log(`Row ${rowIndex}: Missing task ID, skipping relationship`);
                return;
            }

            // Convert IDs to strings for consistent lookups
            const taskIdStr = String(rawTaskId);
            const predIdStr = String(rawPredId);

            // Skip self-references
            if (taskIdStr === predIdStr) {
                console.log(`Row ${rowIndex}: Skipping self-reference for task ${taskIdStr}`);
                skippedRelCount++;
                return;
            }

            // Get task and predecessor objects from our map
            const task = this.taskIdToTask.get(taskIdStr);
            const pred = this.taskIdToTask.get(predIdStr);

            // Skip if either task or predecessor is not found
            if (!task || !pred) {
                if (!task) console.log(`Row ${rowIndex}: Task ${taskIdStr} not found, skipping relationship`);
                if (!pred) console.log(`Row ${rowIndex}: Predecessor ${predIdStr} not found, skipping relationship`);
                skippedRelCount++;
                return;
            }

            // Check if this relationship already exists
            if (task.predecessorIds.includes(predIdStr)) {
                console.log(`Row ${rowIndex}: Duplicate relationship between ${predIdStr} and ${taskIdStr}, skipping`);
                skippedRelCount++;
                return;
            }

            // Get relationship type, default to 'FS'
            const relType = (relTypeIdx !== -1 && row[relTypeIdx] != null) 
                ? String(row[relTypeIdx]) 
                : 'FS';
            
            // Get relationship free float, default to null
            let relFreeFloat: number | null = null;
            if (relFloatIdx !== -1 && row[relFloatIdx] != null) {
                const parsedFloat = Number(row[relFloatIdx]);
                if (!isNaN(parsedFloat)) {
                    relFreeFloat = parsedFloat;
                }
            }

            // Add relationship info
            task.predecessorIds.push(predIdStr);
            task.predecessors.push(pred);
            pred.successors.push(task);
            task.relationshipTypes[predIdStr] = relType;
            task.relationshipFreeFloats[predIdStr] = relFreeFloat;

            // Create relationship object
            const relationship: Relationship = {
                predecessorId: predIdStr,
                successorId: taskIdStr,
                type: relType,
                freeFloat: relFreeFloat,
                isCritical: false // Will be determined later
            };
            
            this.relationships.push(relationship);
            processedRelCount++;
        });

        console.log(`--- Debug: Second pass completed. Processed ${processedRelCount} relationships, skipped ${skippedRelCount} ---`);
        console.log(`--- Debug: Total tasks: ${this.data.length}, Total relationships: ${this.relationships.length} ---`);
    }

    // Critical Path Method calculation
    private calculateCPM(): void {
        console.log("--- Debug: Starting CPM Calculations ---");
        
        if (this.data.length === 0) {
            console.log("--- ERROR: No tasks to perform CPM on ---");
            return;
        }

        // --- 1. Topological Sort (Kahn's Algorithm) ---
        console.log("--- Debug: Starting topological sort ---");
        const sortedTasks: Task[] = [];
        const inDegree = new Map<string, number>();
        const queue: Task[] = [];
        let processedCount = 0;

        // Initialize in-degree for all tasks
        this.data.forEach(task => {
            inDegree.set(task.internalId, task.predecessors.length);
            if (task.predecessors.length === 0) {
                queue.push(task);
            }
        });

        console.log(`--- Debug: Found ${queue.length} start tasks (no predecessors) ---`);
        
        // Process the queue (Kahn's algorithm)
        while (queue.length > 0) {
            const current = queue.shift()!;
            sortedTasks.push(current);
            processedCount++;

            // Process successors
            for (const succ of current.successors) {
                const succId = succ.internalId;
                const currentInDegree = inDegree.get(succId) || 0;
                
                if (currentInDegree > 0) {
                    inDegree.set(succId, currentInDegree - 1);
                    
                    if ((inDegree.get(succId) || 0) === 0) {
                        queue.push(succ);
                    }
                }
            }
        }

        // Handle cycles like in the Python code
        if (processedCount < this.data.length) {
            console.warn(`--- Warning: Cycle detected or disconnected graph. Processed ${processedCount} tasks out of ${this.data.length} ---`);
            
            // Add remaining tasks instead of throwing an error
            const unprocessedTasks = this.data.filter(task => !sortedTasks.includes(task));
            console.warn(`--- Debug: ${unprocessedTasks.length} tasks not processed in topological sort ---`);
            
            // Add unprocessed tasks to the end
            sortedTasks.push(...unprocessedTasks);
            console.log(`--- Debug: Added unprocessed tasks to the end of the sorted list ---`);
        }

        console.log(`--- Debug: Topological sort completed with ${sortedTasks.length} tasks ---`);

        // --- 2. Forward Pass (Early Dates) ---
        console.log("--- Debug: Starting Forward Pass ---");
        
        // Initialize early dates
        this.data.forEach(task => {
            task.earlyStart = 0;
            task.earlyFinish = task.duration;
        });

        // Process tasks in topological order
        for (const task of sortedTasks) {
            let maxPredFinish = 0;
            
            // Calculate earliest start time based on predecessors
            for (const pred of task.predecessors) {
                const predId = pred.internalId;
                const relType = task.relationshipTypes[predId] || 'FS';
                const predDuration = pred.duration || 0;
                const predES = pred.earlyStart;
                const predEF = pred.earlyFinish;
                const taskDuration = task.duration || 0;
                
                let potentialStart = 0;
                
                // Calculate start based on relationship type
                switch (relType) {
                    case 'FS': // Finish-to-Start: Predecessor must finish before successor starts
                        potentialStart = predEF;
                        break;
                    case 'SS': // Start-to-Start: Predecessor must start before successor starts
                        potentialStart = predES;
                        break;
                    case 'FF': // Finish-to-Finish: Predecessor must finish before successor finishes
                        potentialStart = predEF - taskDuration;
                        break;
                    case 'SF': // Start-to-Finish: Predecessor must start before successor finishes
                        potentialStart = predES - taskDuration;
                        break;
                    default:
                        potentialStart = predEF; // Default to FS
                        break;
                }
                
                maxPredFinish = Math.max(maxPredFinish, potentialStart);
            }
            
            // Update early dates
            task.earlyStart = maxPredFinish;
            task.earlyFinish = task.earlyStart + (task.duration || 0);
        }
        
        console.log("--- Debug: Forward Pass completed ---");

        // --- 3. Calculate project end date ---
        const projectEnd = Math.max(...this.data.map(task => task.earlyFinish || 0));
        console.log(`--- Debug: Project end date: ${projectEnd} ---`);

        // --- 4. Initialize Late Dates ---
        console.log("--- Debug: Initializing Late Dates ---");
        
        // Initialize late dates to infinity for all tasks
        this.data.forEach(task => {
            task.lateStart = Infinity;
            task.lateFinish = Infinity;
        });
        
        // Initialize end tasks (no successors) to project end date
        const endTasks = this.data.filter(task => task.successors.length === 0);
        
        if (endTasks.length === 0) {
            // If no end tasks found, use tasks with minimum number of successors
            const minSuccCount = Math.min(...this.data.map(t => t.successors.length));
            const pseudoEndTasks = this.data.filter(t => t.successors.length === minSuccCount);
            
            console.log(`--- Debug: No true end tasks found. Using ${pseudoEndTasks.length} tasks with minimum successor count (${minSuccCount}) ---`);
            
            pseudoEndTasks.forEach(task => {
                task.lateFinish = projectEnd;
                task.lateStart = task.lateFinish - (task.duration || 0);
            });
        } else {
            console.log(`--- Debug: Found ${endTasks.length} end tasks ---`);
            
            endTasks.forEach(task => {
                task.lateFinish = projectEnd;
                task.lateStart = task.lateFinish - (task.duration || 0);
            });
        }

        // --- 5. Backward Pass (Late Dates) ---
        console.log("--- Debug: Starting Backward Pass ---");
        
        // Process tasks in reverse topological order
        const reverseSortedTasks = [...sortedTasks].reverse();
        
        for (const task of reverseSortedTasks) {
            const taskId = task.internalId;
            const taskDuration = task.duration || 0;
            
            // Skip if late dates already calculated (end tasks)
            if (task.lateStart !== Infinity && task.lateFinish !== Infinity) {
                continue;
            }
            
            // Calculate latest finish time based on successors
            let minSuccStart = Infinity;
            
            // Fix: Properly handle relationship types in backward pass 
            if (task.successors.length > 0) {
                const successorsFinishes: number[] = [];
                
                for (const succ of task.successors) {
                    if (succ.lateStart === Infinity || succ.lateFinish === Infinity) {
                        // Skip successors that don't have late dates yet
                        console.warn(`--- Warning: Successor task ${succ.internalId} of task ${taskId} has invalid late dates. Possible cycle. ---`);
                        continue;
                    }
                    
                    // IMPORTANT: Use relationship type from task's perspective looking at successor
                    const relData = this.relationships.find(r => 
                        r.predecessorId === taskId && r.successorId === succ.internalId);
                    const relType = relData?.type || 'FS';
                    const succDuration = succ.duration || 0;
                    const succLS = succ.lateStart;
                    const succLF = succ.lateFinish;
                    
                    let finish = Infinity;
                    
                    // Calculate finish based on relationship type
                    switch (relType) {
                        case 'FS': // Finish-to-Start: Successor can't start until predecessor finishes
                            finish = succLS;
                            break;
                        case 'SS': // Start-to-Start: Successor can't start until predecessor starts
                            finish = succLS + taskDuration; // Predecessor finish = start + duration
                            break;
                        case 'FF': // Finish-to-Finish: Successor can't finish until predecessor finishes
                            finish = succLF;
                            break;
                        case 'SF': // Start-to-Finish: Successor can't finish until predecessor starts
                            finish = succLF - succDuration + taskDuration;
                            break;
                        default:
                            finish = succLS; // Default to FS
                            break;
                    }
                    
                    successorsFinishes.push(finish);
                }
                
                // If we found valid successors, use the minimum value
                if (successorsFinishes.length > 0) {
                    minSuccStart = Math.min(...successorsFinishes);
                } else {
                    // If no valid successors, use project end (fallback)
                    minSuccStart = projectEnd;
                    console.warn(`--- Warning: Task ${taskId} has no valid successors for backward pass. Using project end date. ---`);
                }
            } else {
                // Task has no successors - should have been set in initialization
                if (task.lateFinish === Infinity) {
                    console.warn(`--- Warning: End task ${taskId} not properly initialized. Setting to project end. ---`);
                    minSuccStart = projectEnd;
                }
            }
            
            // Update late dates
            task.lateFinish = minSuccStart;
            task.lateStart = Math.max(0, task.lateFinish - taskDuration);
        }
        
        console.log("--- Debug: Backward Pass completed ---");

        // --- 6. Calculate Total Float and Critical Path ---
        console.log("--- Debug: Starting Criticality Calculations ---");
        
        // Calculate total float for all tasks
        this.data.forEach(task => {
            if (task.lateStart === Infinity || task.earlyStart === undefined) {
                task.totalFloat = Infinity;
                task.isCriticalByFloat = false;
            } else {
                task.totalFloat = task.lateStart - task.earlyStart;
                // Task is critical if total float is zero (or very close to zero)
                task.isCriticalByFloat = Math.abs(task.totalFloat) <= this.floatTolerance;
            }
            
            // Initialize relationship criticality flag
            task.isCriticalByRel = false;
        });
        
        // Calculate relationship criticality based on free float or dates
        let relationshipsWithFloat = 0;
        let criticalRelationships = 0;
        
        this.relationships.forEach(rel => {
            const pred = this.taskIdToTask.get(rel.predecessorId);
            const succ = this.taskIdToTask.get(rel.successorId);
            
            if (!pred || !succ) {
                rel.isCritical = false;
                return;
            }
            
            // Check if relationship is critical based on free float
            if (rel.freeFloat !== null) {
                relationshipsWithFloat++;
                rel.isCritical = Math.abs(rel.freeFloat) <= this.floatTolerance;
            } else {
                // Fallback to checking dates if free float is not available
                const relType = rel.type || 'FS';
                const predES = pred.earlyStart || 0;
                const predEF = pred.earlyFinish || 0;
                const succES = succ.earlyStart || 0;
                const succEF = succ.earlyFinish || 0;
                
                switch (relType) {
                    case 'FS':
                        rel.isCritical = Math.abs(predEF - succES) <= this.floatTolerance;
                        break;
                    case 'SS':
                        rel.isCritical = Math.abs(predES - succES) <= this.floatTolerance;
                        break;
                    case 'FF':
                        rel.isCritical = Math.abs(predEF - succEF) <= this.floatTolerance;
                        break;
                    case 'SF':
                        rel.isCritical = Math.abs(predES - succEF) <= this.floatTolerance;
                        break;
                    default:
                        rel.isCritical = Math.abs(predEF - succES) <= this.floatTolerance;
                }
            }
            
            // If relationship is critical, mark both tasks as critical by relationship
            if (rel.isCritical) {
                criticalRelationships++;
                pred.isCriticalByRel = true;
                succ.isCriticalByRel = true;
            }
        });
        
        console.log(`--- Debug: Found ${relationshipsWithFloat} relationships with free float data ---`);
        console.log(`--- Debug: Identified ${criticalRelationships} critical relationships ---`);
        
        // Final criticality determination - a task is critical if either condition is true
        let criticalTasksCount = 0;
        this.data.forEach(task => {
            task.isCritical = task.isCriticalByFloat || task.isCriticalByRel;
            if (task.isCritical) criticalTasksCount++;
        });
        
        console.log(`--- Debug: Identified ${criticalTasksCount} critical tasks ---`);
        console.log("--- Debug: CPM calculations completed ---");
    }

    // Setup SVG and scales for visualization
    private setupSVGAndScales(viewport: IViewport, tasksToShow: Task[]): { xScale: d3.ScaleLinear<number, number>, yScale: d3.ScaleBand<string> } {
        console.log("--- Debug: Setting up SVG and scales ---");
        
        // Update margins based on settings
        this.margin.left = this.settings.visualSettings.leftMarginValue();
        
        const viewportWidth = (viewport && viewport.width) ? viewport.width : 800;
        const taskCount = tasksToShow.length;
        const chartHeight = Math.max(50, taskCount * (this.taskHeight + this.taskPadding));
        const totalHeight = chartHeight + this.margin.top + this.margin.bottom;
        
        this.svg
            .attr("width", viewportWidth)
            .attr("height", totalHeight);
            
        this.mainGroup
            .attr("transform", `translate(${this.margin.left}, ${this.margin.top})`);
        
        const chartWidth = viewportWidth - this.margin.left - this.margin.right;
        
        // Determine x-axis domain
        const minStart = Math.min(...tasksToShow.map(d => d.earlyStart || 0));
        const maxEnd = Math.max(...tasksToShow.map(d => d.earlyFinish || 0));
        const xDomainPadding = Math.max(5, (maxEnd - minStart) * 0.1);
        
        // Create scales
        const xScale = d3.scaleLinear()
            .domain([Math.max(0, minStart - xDomainPadding), maxEnd + xDomainPadding])
            .range([0, chartWidth]);
            
        const yScale = d3.scaleBand()
            .domain(tasksToShow.map((_, i) => i.toString()))
            .range([0, chartHeight])
            .padding(0.2);
            
        console.log("--- Debug: SVG and scales setup complete ---");
        
        return { xScale, yScale };
    }

    // Draw all visual elements
    private drawVisualElements(tasksToShow: Task[]): void {
        console.log("--- Debug: Drawing visual elements ---");
        
        // Get viewport dimensions
        const svgNode = this.svg.node() as SVGSVGElement;
        const width = svgNode ? svgNode.clientWidth : 800;
        const height = svgNode ? svgNode.clientHeight : 600;
        const chartWidth = width - this.margin.left - this.margin.right;
        
        // Setup scales
        const scaleInfo = this.setupSVGAndScales({ width, height } as IViewport, tasksToShow);
        const xScale = scaleInfo.xScale;
        const yScale = scaleInfo.yScale;
        
        // Get colors from settings
        const taskColor = this.settings.visualSettings.taskColorValue(); 
        const criticalColor = this.settings.visualSettings.criticalPathColorValue();
        const milestoneColor = this.settings.visualSettings.milestoneColorValue();
        const labelColor = this.settings.visualSettings.labelColorValue();
        const showDuration = this.settings.visualSettings.showDurationValue();
        const fontSize = this.settings.visualSettings.fontSizeValue();
        const maxLabelLength = this.settings.visualSettings.maxLabelLengthValue();
        
        // Draw task bars and milestones
        this.drawTasks(tasksToShow, xScale, yScale, taskColor, milestoneColor, 
                       criticalColor, fontSize, labelColor, showDuration, maxLabelLength);
        
        // Draw relationships
        this.drawArrows(tasksToShow, xScale, yScale, criticalColor);
        
        // Draw project end line
        const projectEnd = Math.max(...tasksToShow.map(d => d.earlyFinish || 0));
        this.drawProjectEndLine(projectEnd, chartWidth - this.margin.left, xScale, tasksToShow);
        
        // Draw project duration text
        const criticalPathDuration = this.calculateCriticalPathDuration(this.data);
        this.drawDurationText(chartWidth, height, criticalPathDuration);
        
        console.log("--- Debug: Visualization completed ---");
    }

    // Draw task bars and labels with improved label handling
    private drawTasks(
        tasks: Task[], 
        xScale: d3.ScaleLinear<number, number>, 
        yScale: d3.ScaleBand<string>, 
        taskColor: string, 
        milestoneColor: string, 
        criticalColor: string,
        fontSize: number,
        labelColor: string,
        showDuration: boolean,
        maxLabelLength: number
    ): void {
        console.log(`--- Debug: Drawing ${tasks.length} tasks ---`);
        
        const showTooltips = this.settings.visualSettings.showTooltipsValue();
        const self = this; // For tooltip access in event handlers
        
        // Create task groups
        const taskGroups = this.mainGroup.selectAll<SVGGElement, Task>(".task-group")
            .data(tasks, d => d.internalId)
            .enter()
            .append("g")
            .attr("class", "task-group")
            .attr("transform", (d, i) => `translate(0, ${yScale(i.toString()) || 0})`);
        
        // Draw task bars (not for milestones)
        taskGroups.filter(d => d.type !== 'TT_Mile' && d.type !== 'TT_FinMile')
            .append("rect")
            .attr("class", "task-bar")
            .attr("x", d => xScale(Math.max(0, d.earlyStart || 0)))
            .attr("y", 0)
            .attr("width", d => {
                const width = xScale((d.earlyFinish || 0)) - xScale((d.earlyStart || 0));
                return Math.max(1, width); // Ensure minimum width for visibility
            })
            .attr("height", yScale.bandwidth())
            .attr("rx", 2) // Rounded corners
            .attr("ry", 2)
            .style("fill", d => d.isCritical ? criticalColor : taskColor)
            .style("stroke", "#000")
            .style("stroke-width", 0.5)
            .style("opacity", 0.8);
        
        // Draw milestone diamonds
        taskGroups.filter(d => d.type === 'TT_Mile' || d.type === 'TT_FinMile')
            .append("path")
            .attr("class", "milestone")
            .attr("transform", d => `translate(${xScale(d.earlyStart || 0)}, ${yScale.bandwidth() / 2})`)
            .attr("d", d => {
                const size = this.milestoneSize;
                return `M 0,-${size/2} L ${size/2},0 L 0,${size/2} L -${size/2},0 Z`;
            })
            .style("fill", d => d.isCritical ? criticalColor : milestoneColor)
            .style("stroke", "#000")
            .style("stroke-width", 0.5);
        
        // Add task names with improved truncation and tooltips
        taskGroups.append("text")
            .attr("class", "task-label")
            .attr("x", -this.labelOffset)
            .attr("y", yScale.bandwidth() / 2)
            .attr("text-anchor", "end")
            .attr("dominant-baseline", "central")
            .style("font-size", `${fontSize}px`)
            .style("fill", labelColor)
            .text(d => this.truncateText(d.name, maxLabelLength))
            .each(function(d) {
                // Add tooltip for full task name if truncated
                if (showTooltips && d.name.length > maxLabelLength) {
                    d3.select(this)
                        .on("mouseover", function(event) {
                            d3.select(".tooltip")
                                .style("visibility", "visible")
                                .html(`<strong>Task:</strong> ${d.name}<br>
                                       <strong>Duration:</strong> ${d.duration} days<br>
                                       <strong>Critical:</strong> ${d.isCritical ? 'Yes' : 'No'}<br>
                                       <strong>Early Start:</strong> ${d.earlyStart}<br>
                                       <strong>Early Finish:</strong> ${d.earlyFinish}<br>
                                       <strong>Float:</strong> ${d.totalFloat === Infinity ? 'N/A' : d.totalFloat}`)
                                .style("left", (event.pageX + 15) + "px")
                                .style("top", (event.pageY - 30) + "px");
                        })
                        .on("mousemove", function(event) {
                            d3.select(".tooltip")
                                .style("left", (event.pageX + 15) + "px")
                                .style("top", (event.pageY - 30) + "px");
                        })
                        .on("mouseout", function() {
                            d3.select(".tooltip").style("visibility", "hidden");
                        });
                }
            });
        
        // Add finish dates
        taskGroups.append("text")
            .attr("class", "finish-date")
            .attr("x", d => {
                const x = d.type === 'TT_Mile' || d.type === 'TT_FinMile' 
                    ? xScale(d.earlyStart || 0) + this.milestoneSize
                    : xScale((d.earlyFinish || 0)) + this.dateLabelOffset;
                return x;
            })
            .attr("y", yScale.bandwidth() / 2)
            .attr("text-anchor", "start")
            .attr("dominant-baseline", "central")
            .style("font-size", `${fontSize * 0.85}px`)
            .style("fill", labelColor)
            .text(d => d.finishDateStr || "");
        
        // Add duration text (if enabled)
        if (showDuration) {
            taskGroups.filter(d => d.type !== 'TT_Mile' && d.type !== 'TT_FinMile' && (d.duration || 0) > 0)
                .append("text")
                .attr("class", "duration-text")
                .attr("x", d => xScale((d.earlyStart || 0)) + (xScale((d.earlyFinish || 0)) - xScale((d.earlyStart || 0))) / 2)
                .attr("y", yScale.bandwidth() / 2)
                .attr("text-anchor", "middle")
                .attr("dominant-baseline", "central")
                .style("font-size", `${fontSize * 0.8}px`)
                .style("fill", "white")
                .style("font-weight", "bold")
                .style("pointer-events", "none")
                .text(d => {
                    const width = xScale((d.earlyFinish || 0)) - xScale((d.earlyStart || 0));
                    // Only show text if there's enough space
                    return width > fontSize * 3 ? `${Math.round(d.duration || 0)}d` : "";
                });
        }
        
        console.log("--- Debug: Task drawing complete ---");
    }

    // Draw relationship arrows
    private drawArrows(
        tasks: Task[], 
        xScale: d3.ScaleLinear<number, number>, 
        yScale: d3.ScaleBand<string>, 
        criticalColor: string
    ): void {
        console.log("--- Debug: Drawing relationship arrows ---");
        
        // Create a map for task positions
        const taskPositions = new Map<string, number>();
        tasks.forEach((task, index) => {
            taskPositions.set(task.internalId, index);
        });
        
        // Filter relationships where both tasks are visible
        const visibleRelationships = this.relationships.filter(rel => {
            return taskPositions.has(rel.predecessorId) && taskPositions.has(rel.successorId);
        });
        
        console.log(`--- Debug: Drawing ${visibleRelationships.length} relationships ---`);
        
        // Draw arrows for each relationship
        this.mainGroup.selectAll(".relationship-arrow")
            .data(visibleRelationships)
            .enter()
            .append("path")
            .attr("class", d => `relationship-arrow ${d.isCritical ? "critical" : "normal"}`)
            .attr("d", d => {
                const predIndex = taskPositions.get(d.predecessorId);
                const succIndex = taskPositions.get(d.successorId);
                
                if (predIndex === undefined || succIndex === undefined) {
                    return "";
                }
                
                const pred = this.taskIdToTask.get(d.predecessorId);
                const succ = this.taskIdToTask.get(d.successorId);
                
                if (!pred || !succ) {
                    return "";
                }
                
                const predY = (yScale(predIndex.toString()) || 0) + yScale.bandwidth() / 2;
                const succY = (yScale(succIndex.toString()) || 0) + yScale.bandwidth() / 2;
                
                // Determine start X based on task type
                const predX = pred.type === 'TT_Mile' || pred.type === 'TT_FinMile'
                    ? xScale(pred.earlyStart || 0)
                    : xScale(pred.earlyFinish || 0);
                    
                // Determine end X based on task type
                const succX = succ.type === 'TT_Mile' || succ.type === 'TT_FinMile'
                    ? xScale(succ.earlyStart || 0)
                    : xScale(succ.earlyStart || 0);
                
                // Create path with elbow
                const offset = this.arrowHeadSize;
                
                if (Math.abs(predX - succX) < 20 && succY > predY) {
                    // For close horizontal alignment with successor below, use vertical line
                    return `M${predX},${predY} V${succY - offset}`;
                } else {
                    // Standard elbow path
                    const midX = predX + Math.min(40, (succX - predX) / 2);
                    return `M${predX},${predY} H${midX} V${succY} H${succX - offset}`;
                }
            })
            .attr("fill", "none")
            .attr("stroke", d => d.isCritical ? criticalColor : "#555")
            .attr("stroke-width", d => d.isCritical ? this.criticalArrowStrokeWidth : this.arrowStrokeWidth)
            .attr("marker-end", d => d.isCritical ? "url(#arrowhead-critical)" : "url(#arrowhead)");
            
        console.log("--- Debug: Arrow drawing complete ---");
    }

    // Draw project end line
    private drawProjectEndLine(projectEnd: number, chartWidth: number, xScale: d3.ScaleLinear<number, number>, tasks: Task[]): void {
        console.log(`--- Debug: Drawing project end line at ${projectEnd} ---`);
        
        const endX = xScale(projectEnd);
        const chartHeight = this.mainGroup.node()?.getBBox()?.height || 100;
        
        // Draw end line
        this.mainGroup.append("line")
            .attr("class", "project-end-line")
            .attr("x1", endX)
            .attr("y1", -10)
            .attr("x2", endX)
            .attr("y2", chartHeight + 10)
            .attr("stroke", "green")
            .attr("stroke-width", 1.5)
            .attr("stroke-dasharray", "5,3");
        
        // Add end date label
        let endDateText = `Day ${Math.round(projectEnd)}`;
        
        // Try to find the last task's date if available
        const lastCriticalTask = [...tasks].sort((a, b) => (b.earlyFinish || 0) - (a.earlyFinish || 0))[0];
        if (lastCriticalTask && lastCriticalTask.finishDateStr) {
            endDateText = `Est. End: ${lastCriticalTask.finishDateStr}`;
        }
        
        this.mainGroup.append("text")
            .attr("class", "project-end-label")
            .attr("x", endX + 5)
            .attr("y", -5)
            .attr("text-anchor", "start")
            .style("fill", "green")
            .style("font-size", this.settings.visualSettings.fontSizeValue() + "px")
            .style("font-weight", "bold")
            .text(endDateText);
            
        console.log("--- Debug: Project end line drawn ---");
    }

    // Draw duration text
    private drawDurationText(chartWidth: number, height: number, duration: number): void {
        console.log(`--- Debug: Drawing duration text: ${duration} days ---`);
        
        this.svg.append("text")
            .attr("class", "project-duration-text")
            .attr("x", this.margin.left + chartWidth / 2)
            .attr("y", height - this.margin.bottom / 2)
            .attr("text-anchor", "middle")
            .attr("dominant-baseline", "middle")
            .style("font-size", (this.settings.visualSettings.fontSizeValue() + 1) + "px")
            .style("font-weight", "bold")
            .style("fill", "#333")
            .text(`Longest Path Duration: ${Math.round(duration)} days`);
            
        console.log("--- Debug: Duration text drawn ---");
    }

    // Calculate critical path duration (sum of critical tasks)
    private calculateCriticalPathDuration(tasks: Task[]): number {
        const criticalTasks = tasks.filter(t => t.isCritical);
        return criticalTasks.reduce((sum, task) => sum + (task.duration || 0), 0);
    }

    // Helper function to check if a data role exists
    private hasDataRole(dataView: DataView, roleName: string): boolean {
        if (!dataView || !dataView.metadata || !dataView.metadata.columns) {
            return false;
        }
        return dataView.metadata.columns.some(col => col.roles && col.roles[roleName]);
    }

    // Helper function to get column index by role
    private getColumnIndex(dataView: DataView, roleName: string): number {
        if (!dataView || !dataView.metadata || !dataView.metadata.columns) {
            return -1;
        }
        return dataView.metadata.columns.findIndex(col => col.roles && col.roles[roleName]);
    }

    // Helper for date formatting
    private cleanDate(dateStr: string): string {
        if (typeof dateStr !== 'string') {
            return String(dateStr);
        }
        
        if (dateStr.includes('T')) {
            return dateStr.split('T')[0];
        }
        
        // Try to parse as Date if not in ISO format
        try {
            const date = new Date(dateStr);
            if (!isNaN(date.getTime())) {
                return date.toISOString().split('T')[0];
            }
        } catch (e) {
            // Ignore parse errors
        }
        
        return dateStr;
    }

    // Limit tasks for display with improved algorithm
    private limitTasks(tasksToFilter: Task[], maxTasks: number): Task[] {
        console.log(`--- Debug: Limiting tasks, input: ${tasksToFilter.length}, max: ${maxTasks} ---`);
        
        const effectiveMaxTasks = Math.max(1, maxTasks || this.defaultMaxTasks);
        
        if (tasksToFilter.length <= effectiveMaxTasks) {
            console.log("--- Debug: No limiting needed ---");
            return [...tasksToFilter];
        }
        
        // Prioritize start, end, and milestones
        const countForEnds = Math.min(8, Math.floor(effectiveMaxTasks / 2));
        const actualStartCount = Math.min(countForEnds, Math.floor(tasksToFilter.length / 2));
        const actualEndCount = Math.min(countForEnds, tasksToFilter.length - actualStartCount);
        
        console.log(`--- Debug: Will show ${actualStartCount} start tasks and ${actualEndCount} end tasks ---`);
        
        const startTasks = tasksToFilter.slice(0, actualStartCount);
        const endIndex = Math.max(actualStartCount, tasksToFilter.length - actualEndCount);
        const endTasks = tasksToFilter.slice(endIndex);
        
        // Get middle tasks with priority to milestones
        const middleTasks = tasksToFilter.slice(actualStartCount, endIndex);
        const milestones = middleTasks.filter(t => t.type === 'TT_Mile' || t.type === 'TT_FinMile');
        const regularTasks = middleTasks.filter(t => t.type !== 'TT_Mile' && t.type !== 'TT_FinMile');
        
        console.log(`--- Debug: Found ${milestones.length} milestones and ${regularTasks.length} regular tasks in middle section ---`);
        
        // Calculate how many regular tasks we can include
        const remainingSlots = effectiveMaxTasks - startTasks.length - endTasks.length - milestones.length;
        let selectedRegularTasks: Task[] = [];
        
        if (remainingSlots > 0 && regularTasks.length > 0) {
            if (regularTasks.length <= remainingSlots) {
                selectedRegularTasks = [...regularTasks];
            } else {
                // Sample evenly from regular tasks
                const indices = Array.from(
                    { length: Math.min(remainingSlots, regularTasks.length) },
                    (_, i) => Math.floor(i * regularTasks.length / remainingSlots)
                );
                selectedRegularTasks = indices.map(i => regularTasks[i]);
            }
        }
        
        // Combine all selected tasks and remove duplicates
        const combined = [...startTasks, ...milestones, ...selectedRegularTasks, ...endTasks];
        const uniqueTasks = Array.from(
            new Map(combined.map(task => [task.internalId, task])).values()
        );
        
        // Sort by early start
        const result = uniqueTasks.sort((a, b) => (a.earlyStart || 0) - (b.earlyStart || 0));
        
        console.log(`--- Debug: Limited to ${result.length} tasks after filtering ---`);
        return result;
    }

    // Helper to truncate text
    private truncateText(text: string | null | undefined, maxLength: number): string {
        if (!text) return '';
        return text.length > maxLength ? text.substring(0, maxLength - 3) + '...' : text;
    }

    // Display message in the visual (for errors or empty states)
    private displayMessage(message: string): void {
        console.log(`--- Message: ${message} ---`);
        
        const width = this.svg.node()?.clientWidth || 300;
        const height = this.svg.node()?.clientHeight || 200;
        
        this.svg.append("text")
            .attr("class", "message-text")
            .attr("x", width / 2)
            .attr("y", height / 2)
            .attr("text-anchor", "middle")
            .attr("dominant-baseline", "middle")
            .style("fill", "#d00")
            .style("font-size", "14px")
            .style("font-weight", "bold")
            .text(message);
    }

    // Enumerate objects for property pane
    public enumerateObjectInstances(options: EnumerateVisualObjectInstancesOptions): VisualObjectInstanceEnumeration {
        const objectName = options.objectName;
        const objectEnumeration: VisualObjectInstance[] = [];
        
        if (!this.settings || !this.settings.visualSettings) {
            return objectEnumeration;
        }
        
        switch (objectName) {
            case 'visualSettings':
                objectEnumeration.push({
                    objectName: objectName,
                    properties: {
                        taskColor: this.settings.visualSettings.taskColor,
                        criticalPathColor: this.settings.visualSettings.criticalPathColor,
                        milestoneColor: this.settings.visualSettings.milestoneColor,
                        fontSize: this.settings.visualSettings.fontSize,
                        labelColor: this.settings.visualSettings.labelColor,
                        maxLabelLength: this.settings.visualSettings.maxLabelLength,
                        maxTasksToShow: this.settings.visualSettings.maxTasksToShow,
                        leftMargin: this.settings.visualSettings.leftMargin,
                        showDuration: this.settings.visualSettings.showDuration,
                        showTooltips: this.settings.visualSettings.showTooltips
                    },
                    selector: null
                });
                break;
        }
        
        return objectEnumeration;
    }
}